.services {
    position: relative;
    padding-top: 100px ;
    padding-bottom: 100px;
    background-color: var(--color-bg);
    overflow: hidden;
  }

  @media (min-width:900px){
    .services{
      margin-top: -72px;
    }

  }

  .services .info{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  .services .info p{
    color: var(--color-white);
    margin-bottom: 150px;
    font-size: 28px;
    line-height: 48px;
} 

@media  (max-width:767px){
  .services .info p{
    font-size: 18px;
    line-height: 30px;
    margin-top: -30px;
    text-align: center;
  }
}

.services .info span{
    position: absolute;
    color:var(--color-primary);
    left: 20%;
    top: 50%;
    letter-spacing: 3px;
}

@media  (max-width:991px){
  .services .info p{
    text-align: center;
    
    
  }
  .services .info span{
    margin-top: 35px;
  }
  
}

@media  (max-width:767px){
  .services .info span {
    margin: 0 auto;

  }
}

.slide-right {
    animation: 3s slide-right;
    transition: 3s;
    -webkit-transition: 3s;
    -moz-transition: 3s;
    -ms-transition: 3s;
    -o-transition: 3s;
}

.services .info span::before{
    content: "";
    position:absolute;
    margin-top: 13px;
    left: -100px;
    width: 70px;
    height: 500px;
    border-top:2px solid var(--color-primary-variant) ;
}


  .services .container{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px , 1fr));
    grid-gap: 40px;
  }

  @media  (max-width:767px){
    .services .container {
      display: grid;
      grid-gap: 60px;
      align-items: center;
      justify-content: center;

    }
  }
  

  .services .srv{
    margin-top: -25px;
    padding: 30px;
    text-align: center;
    border-radius: 6px;
    box-shadow: 0px 1px 10px 7px rgb(170 170 170 / 20%);
    -webkit-box-shadow: 0px 1px 10px 7px rgb(170 170 170 / 20%);
    -moz-box-shadow: 0px 1px 10px 7px rgb(170 170 170 / 20%);
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;
    -ms-border-radius: 6px;
    -o-border-radius: 6px;
}

@media (max-width:767px){
  .services .srv{
    width: 76%;
    margin:auto;
}
}
  .services .srv:hover{
    background-color: var(--color-bg-variant);
    z-index: 2;
    -webkit-transition: all 200ms ease-in;
    -webkit-transform: scale(1.1);
    -ms-transition: all 200ms ease-in;
    -ms-transform: scale(1.1);   
    -moz-transition: all 200ms ease-in;
    -moz-transform: scale(1.1);
    transition: all 200ms ease-in;
    transform: scale(1.1);
    border: 2px solid var(--color-primary-variant);
  }
  
  .services .srv .icon {
    font-size: 32px;
  }
  
  .services .srv h3 {
    font-weight: 800;
    margin: 30px 0;
    color: var(--color-primary);
  }
  
  .services .srv p {
  line-height: 1.9;
  color:var(--color-light);
  font-size: 18px;

  }

  .services .srv .more {
    margin-top: 25px ;
  }

/* ---------- Animation -----------*/
/***** Slide Right *****/

  @keyframes slide-right {
    from {
      margin-left: -100%;
      
    }
    to {
      margin-left: 0%;
      opacity: 1;
    }
  }



